.game-result-container{display:flex;justify-content:center;align-items:center;position:absolute;top:0;right:0;width:100%;height:100%;background-color:rgba(137,156,177,.1843137255)}.game-result-container .game-result{display:flex;flex-direction:column;align-items:center;position:relative;align-self:center;width:100%;padding:48px;background-color:#fff;scale:0;animation:result-animation 700ms ease-in-out 0s forwards}.game-result-container .game-result.success{background-image:url("./congrats.avif");background-repeat:repeat}.game-result-container .game-result img{animation:img-animation 700ms ease-in-out 0s forwards}.game-result-container .game-result h1{margin-top:24px;margin-bottom:16px}.game-result-container .game-result p{margin-bottom:32px;color:#899cb1}@keyframes result-animation{to{scale:1}}@keyframes img-animation{to{width:initial}}