.profile-edit{position:relative;display:flex;flex-direction:column;align-items:center;gap:24px;width:100%}.profile-edit .profile-image-container{position:relative}.profile-edit .profile-image-container .profile-image{transform:scale(1.17);width:85px;height:85px;-webkit-clip-path:circle();clip-path:circle();-o-object-fit:cover;object-fit:cover}.profile-edit .profile-image-container .image-container-overflow-hidden{overflow:hidden}.profile-edit .profile-image-container .edit-icon-container{position:absolute;z-index:1;right:-10px;bottom:-10px;padding:10px;-webkit-clip-path:circle();clip-path:circle();background-color:#f9f9f9;-webkit-backdrop-filter:drop-shadow(2px 2px 14px black);backdrop-filter:drop-shadow(2px 2px 14px black);cursor:pointer}.profile-edit .profile-image-container .edit-icon-container .edit-icon{width:16px}.profile-edit .profile-image-container .edit-icon-container .edit-icon path{fill:var(--dark-grey-color)}.profile-edit .profile-image-container .edit-icon-container:hover{background-color:var(--light-grey-color)}.profile-edit .inputs-container{display:flex;flex-direction:column;gap:24px}.profile-edit .input-container input{outline:none;border:2px solid var(--light-grey-color);border-radius:10px;padding:12px;font-size:14px}.profile-edit .btns-container{display:flex;gap:24px;margin-top:16px}.profile-edit .btns-container .btn-cancel{border:1px solid var(--pink-color);color:var(--pink-color);background-color:#fff}.profile-edit .btns-container button{padding:18px 36px;font-size:18px}.profile-edit .modal{position:absolute;z-index:2;display:flex;flex-direction:column;gap:24px;top:10%;left:auto;right:auto;background-color:#fff;box-shadow:0px 0px 8px rgba(79,98,113,.24);padding:16px;border-radius:8px;width:min(504px,90%)}.profile-edit .modal.hidden{display:none}.profile-edit .modal .modal-header{display:flex;align-items:center}.profile-edit .modal .modal-header h4{margin:0 auto}.profile-edit .modal .modal-header .close-icon{-webkit-clip-path:circle();clip-path:circle();cursor:pointer}.profile-edit .modal .modal-header .close-icon path{fill:var(--dark-grey-color)}.profile-edit .modal .modal-header .close-icon:hover{background-color:var(--light-grey-color)}.profile-edit .modal .modal-images-container{display:flex;justify-content:center;align-items:flex-start;flex-wrap:wrap;gap:12px}.profile-edit .modal .modal-images-container .upload-image-container{display:flex;flex-direction:column;justify-content:center;align-items:center;gap:8px;border:1px solid var(--light-grey-color);border-radius:4px;width:85px;height:85px;cursor:pointer}.profile-edit .modal .modal-images-container .upload-image-container p{color:var(--pink-color);font-size:12px}.profile-edit .modal .modal-images-container .upload-image-container .input-image{display:none}.profile-edit .modal .modal-images-container img{display:block;border-radius:4px;width:85px;height:85px;-o-object-fit:cover;object-fit:cover;outline:1px solid rgba(51,51,51,.1);outline-offset:-2px}.profile-edit .modal .modal-images-container img:not(.selected):hover{outline:2px solid var(--light-grey-color);outline-offset:-2px}.profile-edit .modal .modal-images-container .image-container{position:relative;border-radius:4px}.profile-edit .modal .modal-images-container .image-container.selected::before{content:"";z-index:1;position:absolute;border-radius:4px;top:0;right:0;width:99%;height:99%;display:flex;justify-content:center;align-items:center;outline:3px solid var(--pink-color);outline-offset:-2px;background:linear-gradient(rgba(247, 153, 153, 0.3), rgba(247, 153, 153, 0.5))}.profile-edit .modal .modal-images-container .image-container.selected::after{content:url("./images/check-icon.svg");z-index:2;position:absolute;border-radius:4px;top:calc(50% - 14px);right:calc(50% - 14px)}.profile-edit p,.profile-edit h2,.profile-edit h4,.profile-edit label{color:var(--dark-grey-color)}.profile-edit h2{margin-left:auto;cursor:pointer}.profile-edit label{font-size:14px;font-weight:600}