.game-main .current-level-container{display:flex;justify-content:flex-end;align-items:center;gap:42px;font-size:18px;font-weight:bold}.game-main .current-level-container .games-count-container{display:flex;align-items:center;gap:12px}.game-main .current-level-container .games-count-container img{filter:drop-shadow(0px 2px 2px red)}.game-main .welcome-container{display:flex;align-items:center;gap:16px;margin:32px 0}.game-main .welcome-container img{width:48px}.game-main .welcome-container h1{font-weight:600}.game-main .welcome-container h1.child-name{color:var(--pink-color);font-weight:normal}.game-main .games-container{display:flex;flex-direction:column;height:70vh;padding:24px 0;align-items:center;gap:40px;overflow-y:auto;scrollbar-color:var(--pink-color) var(--light-grey-color)}.game-main .games-container::-webkit-scrollbar{width:8px;border-radius:8px}.game-main .games-container::-webkit-scrollbar-track{background:var(--light-grey-color);border-radius:8px}.game-main .games-container::-webkit-scrollbar-thumb{background:var(--pink-color);border-radius:8px}.game-main .games-container::-webkit-scrollbar-thumb:hover{background:var(--pink-color)}.game-main .games-container::-webkit-scrollbar-corner{border-radius:8px}.game-main .games-container .checked-icon-container{filter:drop-shadow(0 0 8px var(--pink-color));cursor:pointer}.game-main .games-container .unlocked-icon-container{position:relative;margin-top:52px;filter:drop-shadow(0px 2px 10px var(--pink-color));cursor:pointer}.game-main .games-container .unlocked-icon-container::after{content:"ابدأ";position:absolute;display:flex;justify-content:center;align-items:center;width:64px;height:53px;color:#fff;background-image:url("../images/chat-box.svg");top:-54px;right:calc(50% - 32px)}.game-main .games-container .unlocked-icon-container:hover{filter:drop-shadow(0px 2px 12px var(--pink-color))}.game-main .games-container .locked-icon-container{filter:drop-shadow(0 2px 10px #899cb1)}.game-main .games-container svg{box-sizing:content-box;padding:20px;-webkit-clip-path:circle();clip-path:circle();pointer-events:none}.game-main .games-container svg.checked{background-color:var(--pink-color)}.game-main .games-container svg.unlocked{background-color:#fff}.game-main .games-container svg.locked{background-color:var(--light-grey-color)}.game-main h1,.game-main p{color:var(--dark-grey-color)}