.chat-game-container{display:flex;flex:1;flex-direction:column;align-items:center;gap:24px}.chat-game-container .chats-container{display:flex;flex-direction:column;margin-top:24px;width:100%;gap:32px}.chat-game-container .chats-container .chat{display:flex;align-items:center;gap:16px}.chat-game-container .chats-container .chat:nth-child(even){align-self:center}.chat-game-container .chats-container .chat:nth-child(even) img{scale:1.12}.chat-game-container .chats-container .chat .image-container-box-shadow{filter:drop-shadow(0px 0px 1px var(--dark-grey-color))}.chat-game-container .chats-container .chat .image-container{overflow:hidden;-webkit-clip-path:circle();clip-path:circle()}.chat-game-container .chats-container .chat img{width:72px;height:72px}.chat-game-container button{margin-top:auto}.message-bubble{border:4px solid var(--dark-grey-color);border-radius:8px;padding:12px 20px;text-align:center;font-weight:bold;color:var(--dark-grey-color);position:relative}.message-bubble:before{content:"";width:0px;height:0px;position:absolute;border-left:10px solid var(--dark-grey-color);border-right:10px solid rgba(0,0,0,0);border-top:10px solid rgba(0,0,0,0);border-bottom:10px solid rgba(0,0,0,0);right:-23px}.message-bubble:after{content:"";width:0px;height:0px;position:absolute;border-left:10px solid #fff;border-right:10px solid rgba(0,0,0,0);border-top:10px solid rgba(0,0,0,0);border-bottom:10px solid rgba(0,0,0,0);right:-17px}