.dashboard{display:flex;min-height:100vh;padding:24px 0 24px 24px;background-color:var(--dark-grey-color)}.dashboard .sidebar{display:flex;flex-direction:column;align-items:flex-start;gap:48px}.dashboard .sidebar img{padding:12px;border-radius:10px;margin-right:80px;background-color:#fff}.dashboard .sidebar ul{display:flex;flex-direction:column;gap:16px;padding-left:32px}.dashboard .sidebar .nav-link{display:flex;align-items:center;gap:20px;padding-right:32px;padding-left:42px;border-top-left-radius:99px;border-bottom-left-radius:99px;color:#fff;font-size:20px;line-height:72px}.dashboard .sidebar .nav-link.active{background-color:var(--pink-color)}.dashboard .sidebar .nav-link:not(.active):hover{background-color:rgba(133,133,133,.0901960784)}.dashboard main{position:relative;display:flex;flex-direction:column;flex:1;border-radius:36px 36px 0 36px;padding:42px;background:linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)),url("./images/background.jpg");background-size:cover}.dashboard main .game-container{display:flex;flex-direction:column;flex:1;text-align:center;gap:60px}.dashboard main .game-container .game-content{display:flex;flex-direction:column;align-items:center;margin:0 auto}.dashboard main .game-container .images-container{display:grid;justify-content:center;justify-items:center;row-gap:80px;grid-template-rows:1fr 1fr;grid-template-columns:min(350px,50%) min(350px,50%);margin-top:60px}.dashboard main .game-container .images-container img{max-width:150px;max-height:150px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;user-select:none}.dashboard main h1{color:var(--dark-grey-color)}