.profile-view{display:flex;flex-direction:column;height:100%;gap:24px}.profile-view .personal-info-container{display:flex;align-items:center;gap:16px;padding:24px;border-radius:6px;background-color:var(--pink-color);box-shadow:0px 0px 8px rgba(79,98,113,.24)}.profile-view .personal-info-container img{width:64px;height:64px}.profile-view .personal-info-container .info{display:flex;flex-direction:column;gap:8px}.profile-view .personal-info-container .info p{color:#fff}.profile-view .personal-info-container .info .name{font-size:24px}.profile-view .personal-info-container .edit-icon{margin-right:auto;cursor:pointer;width:28px}.profile-view .personal-info-container .edit-icon:hover>path{fill:var(--light-grey-color)}.profile-view .progress-container{display:flex;flex-direction:column;gap:24px;padding:32px;box-shadow:0px 0px 8px rgba(79,98,113,.24);border-radius:8px;background-color:#fff}.profile-view .progress-container .progress-header{display:flex;align-items:center;justify-content:space-between}.profile-view .progress-container .progress-cards-container{display:flex;justify-content:space-around;gap:16px}.profile-view .progress-container .progress-card{border:1px solid var(--dark-grey-color);border-radius:8px;padding:8px;display:flex;align-items:center;gap:8px}.profile-view .progress-container .progress-card p.value{margin-right:8px;color:var(--pink-color);font-size:14px;font-weight:bold}.profile-view .progress-container .progress-card img{width:26px}.profile-view .settings-container{display:flex;flex-direction:column;border-radius:8px;box-shadow:0px 0px 8px rgba(79,98,113,.24);background-color:#fff}.profile-view .settings-container h2{padding:24px}.profile-view .settings-container .setting{display:flex;align-items:center;gap:8px;padding:16px 24px;font-size:18px;cursor:pointer}.profile-view .settings-container .setting:hover{background-color:rgba(133,133,133,0.0901960784)}.profile-view .settings-container .setting .toggler{margin-right:auto}.profile-view p,.profile-view h2{color:var(--dark-grey-color)}.profile-view .switch{position:relative;display:inline-block;width:42px;height:22px;margin-right:auto}.profile-view .switch input{opacity:0;width:0;height:0}.profile-view .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:.25s}.profile-view .slider:before{position:absolute;content:"";height:14px;width:14px;left:4px;bottom:4px;background-color:#fff;transition:.25s}.profile-view input:checked+.slider{background-color:var(--pink-color)}.profile-view input:focus+.slider{box-shadow:0 0 1px var(--pink-color)}.profile-view input:checked+.slider:before{transform:translateX(21px)}.profile-view .slider.round{border-radius:20px}.profile-view .slider.round:before{border-radius:50%}