.registration-details-page{display:flex;flex-direction:column;align-items:center;min-height:100vh;background-image:url("../../images/background.webp")}.registration-details-page .card{align-items:stretch}.registration-details-page .gender-preview{display:flex;justify-content:center}.registration-details-page .btn-gender-container{display:flex;width:100%;justify-content:space-evenly}.registration-details-page .btn-gender-container button{position:relative;display:flex;align-items:center;gap:16px;padding:4px 40px;color:#fff}.registration-details-page .btn-gender-container button.selected::before{content:"✓";color:var(--dark-grey-color);background-color:#fff;border-radius:50%;box-shadow:0px 0px 8px rgba(79,98,113,.25);padding:0 4px;position:absolute;right:-12px;top:-12px;width:20px}.registration-details-page form{display:flex;flex-direction:column;gap:20px}.registration-details-page .input-container input{outline:none;border:2px solid var(--light-grey-color);border-radius:10px;padding:12px;font-size:14px}.registration-details-page p,.registration-details-page label{color:var(--dark-grey-color)}.registration-details-page button img{width:24px}.registration-details-page h2{text-align:center;color:var(--pink-color)}@media(max-width: 540px){.registration-details-page{background-image:none}.registration-details-page .card{margin:0;box-shadow:none;border-radius:0;gap:16px}.registration-details-page .card .card-content .input-container.otp-container input{border-radius:8px;width:42px;height:42px}.registration-details-page .card h1{font-size:24px}.registration-details-page .card .gender-preview img{height:170px}.registration-details-page .card .btn-gender-container{justify-content:space-between}.registration-details-page .card .btn-gender-container button{padding:4px 28px}}