.authentication-page{display:flex;padding:32px;gap:16px;min-height:100vh}.authentication-page .side-header{width:min(768px,45%);display:flex;flex-direction:column;align-items:center;border-radius:40px;background-image:url("./images/side-background.webp");background-size:cover}.authentication-page .side-header img{margin:auto 0}.authentication-page main{flex:1;margin:auto 0;display:flex;flex-direction:column;align-items:center;gap:24px}.authentication-page h1,.authentication-page a{color:var(--pink-color)}.authentication-page form{width:min(360px,100%);margin-top:8px;display:flex;flex-direction:column;gap:16px}.authentication-page form label{font-size:14px;font-weight:600}.authentication-page form button{width:100%;padding:14px 0;font-size:14px}.authentication-page form .error{color:red;font-size:14px;text-align:center}.authentication-page .input-container input{outline:none;border:2px solid var(--light-grey-color);border-radius:10px;padding:12px;font-size:14px}.authentication-page a:hover{filter:saturate(150%)}.authentication-page p,.authentication-page label{color:var(--dark-grey-color)}.authentication-page .or-line{margin-top:8px;overflow:hidden;text-align:center}.authentication-page .or-line:before,.authentication-page .or-line:after{background-color:var(--light-grey-color);content:"";display:inline-block;height:1px;position:relative;vertical-align:middle;width:50%}.authentication-page .or-line:before{right:20px;margin-left:-50%}.authentication-page .or-line:after{left:20px;margin-right:-50%}@media(max-width: 800px){.authentication-page .side-header{display:none}}