.forgot-password{display:flex;flex-direction:column;align-items:center;min-height:100vh;background-image:url("../images/background.webp")}.forgot-password .progress-bar-container{display:flex;gap:12px}.forgot-password .progress-bar{width:60px;height:8px;border-radius:6px;background-color:#ffebe5}.forgot-password .progress-bar.active{background-color:var(--pink-color)}.forgot-password .card-content{display:flex;flex-direction:column;align-items:center;gap:24px}.forgot-password .card-content .input-container{display:flex;flex-direction:column;width:100%;gap:8px}.forgot-password .card-content .input-container input{outline:none;border:2px solid var(--light-grey-color);border-radius:10px;padding:12px;font-size:14px}.forgot-password .card-content .input-container.otp-container{flex-direction:row-reverse;justify-content:center}.forgot-password .card-content .input-container.otp-container input{direction:ltr;border-width:1px;width:52px;height:52px;border-radius:12px;font-size:24px;font-weight:600;text-align:center;-moz-appearance:textfield}.forgot-password .card-content .input-container.otp-container input::-webkit-outer-spin-button,.forgot-password .card-content .input-container.otp-container input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.forgot-password h1,.forgot-password a{color:var(--pink-color)}.forgot-password p{text-align:center;color:#899cb1;font-size:14px;line-height:24px}.forgot-password label{color:var(--dark-grey-color)}.forgot-password button{align-self:stretch;padding:14px 0;font-size:14px}@media(max-width: 540px){.forgot-password{background-image:none}.forgot-password .card{margin:0;box-shadow:none;border-radius:0}.forgot-password .card .card-content .input-container.otp-container input{border-radius:8px;width:42px;height:42px}.forgot-password .card h1{font-size:24px}}